body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}




.navbar{background-color:black;width:100%;height:65px;position:fixed;top:0;display:flex;justify-content:center;align-items:center;font-size:1.2rem;position:fixed;top:0;z-index:100}.navbar ul{display:flex;flex-direction:row}.navbar ul li{color:white;margin-left:30px;list-style-type:none;font-weight:bold;font-family:Courier;font-size:20px}.navbar ul li:hover{color:#6fddc1;cursor:pointer}.navbar .active{color:#6fddc1}

*{margin:0;padding:0}.container{width:100%;height:100vh;background:#232323;display:flex;justify-content:center;align-items:center}.container img{width:200px;height:auto;border-radius:50%;margin-right:30px}.container .box{width:265px;height:250px;position:relative;display:flex;justify-content:center;flex-direction:column}.container .box .title{width:100%;position:relative;display:flex;align-items:center;height:50px}.container .box .title .block{width:0%;height:inherit;background:#ffb510;position:absolute;-webkit-animation:mainBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;animation:mainBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;display:flex}.container .box .title h1{font-family:"Poppins";color:#fff;font-size:32px;-webkit-animation:mainFadeIn 2s forwards;animation:mainFadeIn 2s forwards;-webkit-animation-delay:1.6s;animation-delay:1.6s;opacity:0;display:flex;align-items:baseline;position:relative}.container .box .title h1 span{width:0px;height:0px;border-radius:50%;background:#ffb510;-webkit-animation:load 0.6s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;animation:popIn 0.8s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;-webkit-animation-delay:2s;animation-delay:2s;margin-left:5px;margin-top:-10px;position:absolute;bottom:13px;right:-12px}.container .box .role{width:100%;position:relative;display:flex;align-items:center;height:30px;margin-top:-10px}.container .box .role .block{width:0%;height:inherit;background:#e91e63;position:absolute;-webkit-animation:secBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;animation:secBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;-webkit-animation-delay:2s;animation-delay:2s;display:flex}.container .box .role p{-webkit-animation:secFadeIn 2s forwards;animation:secFadeIn 2s forwards;-webkit-animation-delay:3.2s;animation-delay:3.2s;opacity:0;font-weight:400;font-family:"Lato";color:#ffffff;font-size:12px;text-transform:uppercase;letter-spacing:5px}@-webkit-keyframes mainBlock{0%{width:0%;left:0}50%{width:100%;left:0}100%{width:0;left:100%}}@keyframes mainBlock{0%{width:0%;left:0}50%{width:100%;left:0}100%{width:0;left:100%}}@-webkit-keyframes secBlock{0%{width:0%;left:0}50%{width:100%;left:0}100%{width:0;left:100%}}@keyframes secBlock{0%{width:0%;left:0}50%{width:100%;left:0}100%{width:0;left:100%}}@-webkit-keyframes mainFadeIn{0%{opacity:0}100%{opacity:1}}@keyframes mainFadeIn{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes popIn{0%{width:0px;height:0px;background:#e9d856;border:0px solid #ddd;opacity:0}50%{width:10px;height:10px;background:#e9d856;opacity:1;bottom:45px}65%{width:7px;height:7px;bottom:0px;width:15px}80%{width:10px;height:10px;bottom:20px}100%{width:7px;height:7px;background:#e9d856;border:0px solid #222;bottom:13px}}@keyframes popIn{0%{width:0px;height:0px;background:#e9d856;border:0px solid #ddd;opacity:0}50%{width:10px;height:10px;background:#e9d856;opacity:1;bottom:45px}65%{width:7px;height:7px;bottom:0px;width:15px}80%{width:10px;height:10px;bottom:20px}100%{width:7px;height:7px;background:#e9d856;border:0px solid #222;bottom:13px}}@-webkit-keyframes secFadeIn{0%{opacity:0}100%{opacity:0.5}}@keyframes secFadeIn{0%{opacity:0}100%{opacity:0.5}}@-webkit-keyframes top{0%{opacity:0;bottom:-80px}100%{opacity:1;bottom:0px}}@keyframes top{0%{opacity:0;bottom:-80px}100%{opacity:1;bottom:0px}}@-webkit-keyframes icon{0%{opacity:0;-webkit-transform:scale(0);transform:scale(0)}50%{opacity:1;-webkit-transform:scale(1.3) rotate(-2deg);transform:scale(1.3) rotate(-2deg)}100%{opacity:1;bottom:0px}}@keyframes icon{0%{opacity:0;-webkit-transform:scale(0);transform:scale(0)}50%{opacity:1;-webkit-transform:scale(1.3) rotate(-2deg);transform:scale(1.3) rotate(-2deg)}100%{opacity:1;bottom:0px}}

